





























































import common from '../../common/common.js';
import {
	mapState,mapMutations
} from 'vuex';


export default {
	data() {
		return {
			defimg: '../../static/user_3.png',
			userinfo: null,
		}


	},
	
	computed: {
		...mapState(['user']),
	},
	methods: {
		
		...mapMutations(['loginout']),

		getuser: function() {
			if(this.user.user===null)
			{
				this.userinfo=null
				return false
			}
			
			let that = this;
			uni.request({
				url: common.hosturl + "user/getuserinfo.html",
				method: "POST",
				data: {
					token: this.user.user.token,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					
					//console.log(JSON.stringify(res.data))
					if (res.data.code == 0) {

						that.userinfo = res.data.userinfo;

					} else {
						that.userinfo=null;
						return false
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})

		},
		gologinout:function(){
			
			this.loginout();
			uni.showToast({
				title: '退出成功',
				success: function () {
					uni.reLaunch({
						url: "../../pages/manage/login"
					});
				}
			});	
			
		}


	},
	onLoad() {
		this.getuser();

	}

}



